.footer {
  background: url(data:img/png;base64,) no-repeat center;
  padding: 80px 0;
  @media (max-width: $mobile-bp) {
    background-size: cover;
  }
  &__title {
    color: #ffffff;
    margin-bottom: 60px;
  }
  .wrapper {
    margin: 0 auto;
  }
  &__contacts {
    display: flex;
    &--desktop {
      @media (max-width: $mobile-bp) {
        display: none;
      }
    }
    &--mobile {
      display: none;
      @media (max-width: $mobile-bp) {
        display: flex;
        flex-direction: column;
      }
    }
  }
  &__column {
    display: flex;
    flex-direction: column;
    &:not(:last-child){
      margin-right: 15%;
    }
  }
  &__row {
    &:not(:last-child){
      margin-bottom: 50px;
    }
    @media (max-width: $mobile-bp) {
      display: flex;
      justify-content: space-between;
    }
  }
  &__soc_link {
    &:not(:last-child){
      margin-right: 10px;
    }
  }
  &__link {
    color: $white;
    text-decoration: none;
    position: relative;
    white-space: nowrap;
    margin-bottom: 35px;
    display: inline-block;
    &:before {
      content: "";
      background: $white-opacity;
      height: 1px;
      width: 100%;
      position: absolute;
      bottom: -20px;
      transition: $transition-common;
    }
    &:hover:before {
        transform: translateY(-10px);
    }
  }
  &__address_title {
    color: #ffffff;
    margin-bottom: 40px;
  }
  &__text{
    color: #ffffff;
    font-size: 0.73rem;
    &:not(:last-child) {
      margin-bottom: 35px;
    }
  }
  &__logo {
    @media (max-width: $mobile-bp) {
      margin-top: 30px;
    }
  }
}


